.input{
    display: flex;
    width: 70%;
    position: relative;
    align-items: center;
}

.input_box{
    width: 100%;
    border-radius: 3px;
    padding: 15px;
    font-size: 25px;
    border: none;
    transition: 0.2s;
    background-color: #333333;
    color: #ffffff;
    outline: none;
}

.input_submit{
    position: absolute;
    width: 50px;
    height: 50px;
    margin: 12px;
    border-radius: 3px;
    right: -11px;
    border: none;
    font-size: 15px;
    background-color: #A9A9A9;
    color: #ffffff;
    transition: 0.2s all;
}


.todos{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 30px;
    width: 90%;
    flex-direction: column;
}

.todos_single{
    display: flex;
    width: 29.5%;
    border-radius: 5px;
    padding: 20px;
    margin-top: 10px;
    background-color: #333333;
}

.todos__single--text{
    flex: 1;
    padding: 5px;
    border: none;
    font-size: 20px;
}

.todos__single--text:focus{
    outline: none;
}

.iconche{
    margin-left: 10px;
    font-size: 25px;
    cursor: pointer;
}

.iconche:hover{
  color: #98FB98;
}

.icondel{
    margin-left: 10px;
    font-size: 25px;
    cursor: pointer;
}

.icondel:hover{
    color: #ff0000
}

@media (max-width: 700px){
    .input{
        width: 95%;
    }
    .todos{
        width: 95%;
    }
    .todos__single{
        width: 90%;
    }
}

button{
    color: #ffffff;
    background-color: #333333;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ffffff;
}